/* modules */
.seo-text {
  font-size: rem(14);
  color: $add-color;

  &__button {
    padding-top: 10px;
    text-align: center; }

  &__btn {
    background: none;
    text-transform: uppercase;
    border: 0;
    color: $link-color;
    font-size: rem(13);
    outline: none;
    padding: 0;
    margin: 0;
    i {
      margin-left: 10px; } } }

/**/
.subscription {
  // padding: 0
  &__row {
    display: flex;
    background: $add-bg-color url('../img/subscription-border.png') top center repeat-x;
    padding-top: 9px; }

  &__nav {
    display: flex;
    flex-direction: column;
    width: 100%;
    margin: 0;
    padding: 0;
    background: #282324;
    &>li {
      margin: 0;
      padding: 0;
      list-style: none;
      svg {
        path {
          fill: #473e3b; } }

      .subscription__sms__svg {
        width: 20px;
        height: 39px; }

      .subscription__email__svg {

        width: 32px;
        height: 26px; }

      &>a {
        display: flex;
        justify-content: center;
        align-items: center;
        height: 60px;
        line-height: 60px;
        text-align: center;
        border: 1px solid transparent;
        border-right-color: #353031;
        border-bottom-color: #353031;
        transition: color 0.3s ease, background-color 0.3s ease;
        text-decoration: none;
        font-size: rem(30);
        font-family: $font-segoe-light;
        svg {
          &+span {
            display: none;
            color: #927a64;
            line-height: 1;
            text-align: left; } } }
      &.active {
        background: #2e282a;
        a {
          border-right-color: transparent; }
        svg {
          path {
            fill: #796658; } } }
      &.subscription__nav__email {
        a {
          height: 61px;
          line-height: 61px;
          margin-top: -1px;
          border-top-color: #353031;
          border-bottom-color: transparent; }
        &.active {
          a {
            border-top-color: transparent; } } }

      &.subscription__nav__sms {
        &.active {
          a {
            border-bottom-color: transparent; } }
        a {
          border-bottom-color: #353031; } } } }

  &__tab {
    width: 70px; }

  &__body {
    padding-top: 20px;
    width: 100%;
    height: 120px; }

  &__fieldset {
    color: $add-color;
    @include clearfix;
    legend {
      border: 0;
      margin: 0;
      font-size: rem(25);
      line-height: 1.2;
      font-family: $font-segoe-light;
      color: #927a64;
      padding-top: 10px;
      padding-left: 25px; } } }

/**/
.poster {
  width: 100%;
  width: 190px;
  text-align: left;
  color: #d8c7af;
  text-align: center;
  font-size: rem(17);
  margin-bottom: 15px;
  figure {
    position: relative;
    overflow: hidden;
    a {
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      position: absolute;
      z-index: 1; }
    img {
      max-width: 100%;
      display: block;
      transform-origin: 50% 100%;
      transition: all 0.3s ease;
      opacity: 1;
      vertical-align: top; }
    .img {
      background-color: $color-black; }
    figcaption {
      margin-top: 17px;
      height: 74px;
      overflow: hidden; }
    p {
      margin: 0;
      display: inline;
      border-bottom: 1px solid #68615d;
      transition: all 0.3s ease;
      line-height: 1.4; }

    &:hover {
      p {
        border-bottom-color: #d8c7af; } } }
  &:hover {
    figure {
      img {
        transform: scale(1.05);
        opacity: 0.5; } } }
  &--nohover {
    &:hover {
      figure {
        img {
          transform: none;
          opacity: 1; } } } }
  &--document {
    width: 220px; } }

/**/
.schedule {
  &__body {

    &--color {
      padding: 30px 0 15px 0;
      background: #2e282a;
      box-shadow: 115px 0 0 #2e282a, -115px 0 0 #2e282a;

      .schedule__scroll__shadow-left {
        background: {
          image: url('../img/scroll__shadow__left2.png'); } }

      .schedule__scroll__shadow-right {
        background: {
          image: url('../img/scroll__shadow__right2.png'); } } } }

  &__scroll {
    position: relative;

    &__body {
      white-space: nowrap;
      text-align: center;
      overflow: hidden;
      will-change: contents;

      .poster {
        display: inline-block;
        white-space: normal;
        vertical-align: top;
        margin-bottom: 0;
        &+.poster {
          margin-left: 24px; }
        a {
          z-index: 30; }
        .btn {
          z-index: 100;
          margin-top: 20px; } } }

    &__shadow-left, &__shadow-right {
      background: {
        image: url('../img/scroll__shadow__left.png');
        position: top left;
        repeat: repeat-y; }
      position: absolute;
      left: -1px;
      top: 0;
      width: 65px;
      height: calc(100% - 10px);
      z-index: 150; }

    &__shadow-right {
      left: inherit;
      right: -1px;
      background: {
        image: url('../img/scroll__shadow__right.png');
        position: top right; } } }

  &__row {
    display: table;
    &+.schedule__row {
      margin-top: 50px; }
    &>div {
      display: table-cell;
      vertical-align: top; }
    &__left {
      padding-right: 50px; } }

  &__table {
    td {
      vertical-align: top;
      &:last-child {
        .scheduleDay {
          margin-right: 0; } } } } }



