/**/
.catalog-menu {
  @include clearfix;
  &>ul {
    padding: 0;
    margin: 0;
    display: flex;
    flex-wrap: wrap;
    &>li {
      list-style: none;
      width: 33.3%;
      margin-bottom: 30px;
      &>a {
        display: flex;
        align-items: center;
        background: #332C2F;
        border-radius: 3px;
        box-shadow: 0 1px 1px rgba($color-black,0.15);
        margin: 0 15px;
        position: relative;
        padding: 8px;
        text-decoration: none;
        font-size: rem(25);
        font-family: $font-segoe-light;
        transition: all 0.3s linear;
        &:hover {
          background: #5a4940;
          color: #eedbc0; }
        &:before {
          content: '';
          height: 84px;
          width: 84px;
          background: url(../img/catalog-menu.jpg) 0 0 no-repeat;
          border-radius: 3px;
          overflow: hidden;
          flex-shrink: 0; }
        span {
          padding-left: 35px;
